import React from 'react'
import Item from './Item'

let Menu = () => {
    return (
        <div className='main menu'>
            <div className='list'>
                <Item path={'/about'}>
                    <p>About me</p>
                </Item>
                <Item path={'/projects'}>
                    <p>My projects</p>
                </Item>
                <Item path={'/'}>
                    <p>{'< Back'}</p>
                </Item>
            </div>
        </div>
    )
}

export default Menu
