import React, { useLayoutEffect, useRef } from 'react'

let CustomCanvas = ({ targetRef, children, width, height, src, id }) => {
    let newRef = useRef()
    let canvasRef = targetRef ? targetRef : newRef
    
    useLayoutEffect(() => {
        if (src) {
            let img = new Image()
            img.src = src
            img.onload = function () {
                let ctx = canvasRef.current.getContext('2d')
                ctx.drawImage(img, 0, 0, width, height)
            }
        }
    }, [width, height, src, canvasRef])
    return (
        <canvas
            ref={canvasRef}
            {...{ id, width, height, src, }}>
            {children}
        </canvas>
    )
}

export default CustomCanvas
