import React from 'react'

let Item = ({ picture, title, text, tags, repo, sample }) => {
    return (
        <div className="item">
            <div className="image-wrapper">
                <img className="project-image"
                    src={picture}
                    alt={title}
                />
            </div>

            <div className="item-card">
                <h2>{title}</h2>
                <p className="item-body">{text}</p>
                <p className="item-tags">Tags: {tags.join(", ")}</p>
                <div className="item-buttons">
                    <p><a href={repo} target="_blank" rel="noopener noreferrer">Repository</a></p>
                    <p><a href={sample} target="_blank" rel="noopener noreferrer">Try it</a></p>
                </div>
            </div>

        </div>
    )
}

export default Item
