import React from 'react'
import loboBackground from "../../../assets/images/loboBackground.png"
import CustomCanvas from './CustomCanvas'
import AnimatedCanvas from './AnimatedCanvas'

let Container = () => {
    let [width, height] = [1900, 1080]
    return (
        <div className='canvas-wrapper'>
            <CustomCanvas id='origin' width={width} height={height / 2} src={loboBackground} />
            <AnimatedCanvas id='target' width={width} height={height / 2} />
        </div>

    )
}

export default Container
