import React from 'react'
import "../../assets/sass/About.scss"
let About = () => {
    return (
        <div className="main about">
            <div className="flavor">
                <p>Hi! My name is Jose, but Joe is fine.</p>
                <p>Before 'rona I used to travel a lot, and I ended up learning to code in my spare time between jobs.</p>
                <p>I'm looking for work as a developer, either as junior or trainee.</p>
            </div>
            <div className='skills'>
                <div className='front-end'>
                    <h3>Front-end</h3>
                    <p>- HTML/CSS</p>
                    <p>- Javascript</p>
                    <p>- React </p>
                    <p>- Sass</p>
                </div>
                <div className='back-end'>
                    <h3>Back-end</h3>
                    <p>- Node</p>
                    <p>- Express</p>
                    <p>- PostgreSQL</p>
                </div>
            </div>

        </div>
    )
}

export default About
