import React, { useRef } from 'react'
import water from "../../../assets/images/water.png"
import CustomCanvas from './CustomCanvas'
import useAnimate from '../../customHooks/useAnimate'

let AnimatedCanvas = ({ width, height }) => {
    let imageFromRef = useRef()
    let imageToRef = useRef()
    let targetRef = useRef()
    useAnimate(width, height, imageFromRef, imageToRef, targetRef)
    return (
        <CustomCanvas
            targetRef={targetRef}
            id='target'
            width={width}
            height={height}
        >
            <img ref={imageFromRef} id="source-from" src={water} alt="Pixellated water reflection" />
            <img ref={imageToRef} id="source-to" data-src={water} alt="Pixellated water reflection animated" />
        </CustomCanvas>
    )
}

export default AnimatedCanvas
