import { useLayoutEffect } from 'react'
import kampos from "kampos"
//Basic animation loop from https://css-tricks.com/nailing-that-cool-dissolve-transition/

let useAnimate = (width, height, imageFromRef, imageToRef, targetRef) => {
    useLayoutEffect(() => {
        function loadImage(src) {
            return new Promise(resolve => {
                const img = new Image();
                img.onload = function () {
                    resolve(this);
                };
                img.src = src;
            });
        }
        const promisedImages = [
            loadImage(imageFromRef.current.src),
            loadImage(imageToRef.current.dataset.src)
        ];

        const mapTarget = document.createElement('canvas');
        mapTarget.width = width;
        mapTarget.height = height;

        const turbulence = kampos.effects.turbulence({
            noise: kampos.noise.perlinNoise,
            frequency: { x: 0.05, y: 1 },
            octaves: 3,
            isFractal: true,
        })

        const dissolveMap = new kampos.Kampos({
            target: mapTarget,
            effects: [turbulence],
            noSource: true
        });
        dissolveMap.draw();

        const dissolve = kampos.transitions.dissolve();
        dissolve.map = mapTarget;
        dissolve.high = 0.03;

        const displacement = kampos.transitions.displacement()
        displacement.map = mapTarget;
        displacement.sourceScale = { x: 0.025 }
        displacement.toScale = { x: 0.05 }

        const target = targetRef.current;
        const animation = new kampos.Kampos({
            target, effects: [displacement]
        });

        Promise.all(promisedImages).then(([fromImage, toImage]) => {
            animation.setSource({ media: fromImage, width, height });
            displacement.to = toImage
            animation.play(time => {
                displacement.progress = Math.abs(Math.cos(time * 4e-4))
            });
        });

        return () => animation.stop()
    }, [width, height, imageFromRef, imageToRef, targetRef])

    return
}

export default useAnimate
