import React, { useEffect, useCallback } from 'react'
import { useHistory } from 'react-router'

let Front = ({ initialVisit, visit }) => {
    let history = useHistory()
    let travelHome = useCallback(() => history.push('/home'), [history])

    useEffect(() => {
        function autoTravel() {
            let timeout = setTimeout(() => {
                travelHome()
                visit()
            }, 2000)
            return timeout
        }
        let timeout = autoTravel()
        if (!initialVisit) clearTimeout(timeout)

        return () => clearTimeout(timeout)
    }, [initialVisit, visit, history, travelHome])

    return (
        <div className='section front' onClick={travelHome} />

    )
}

export default Front
